.tooltips {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.tooltips .tooltiptext {
  visibility: hidden;
  width: 320px;
  background-color: #1b262c;
  color: #dfd3c3;
  font-size: 0.8rem;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.3s;
}
.tooltips .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 60px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #1b262c transparent transparent transparent;
}
.tooltips:hover .tooltiptext {
  padding:1rem;
  visibility: visible;
  opacity: 1;
}
.tooltips:hover {
color: #1b262c;
transition: 0.5s;
}



/*Work Experience*/
.main-timeline{
    position: relative;
}
.main-timeline:before{
    content: '';
    height: 100%;
    border-left: 3px dashed #475860;
    transform:translateX(-50%);
    position: absolute;
    left: 50%;
    top: 0;
}
.main-timeline .timeline{
    padding-left: 52px;
    display: inline-block;
    position: relative;
    z-index: 1;
}
.main-timeline .timeline:before,
.main-timeline .timeline:after{
    content: '';
    height: 25px;
    width: 25px;
    background-color: #1b262c;
    border-radius: 50%;
    border: 5px solid #475860;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
}
.main-timeline .timeline:after{
    background-color: #475860;
    height: 35px;
    width: 35px;
    border: none;
    border-radius: 0;
    transform: translateX(0) translateY(-50%) rotate(45deg);
    left: 40.5%;
}
.main-timeline .work-experience-date{
    font-style: italic;
    font-size: 0.9rem;
}

.main-timeline .timeline-content{
    background-color: #475860;
    width: 40%;
    border-radius: 10px;
    display: block;
    position: relative;
}
.main-timeline .timeline-content:hover{ text-decoration: none; }
.main-timeline .title{
    text-align: left;
    color: #dfd3c3;
    background-color: #1b262c;
    font-size: 1.5rem;;
    letter-spacing: 1px;
    padding: 1rem;
    margin: 0;
    border-radius: 10px 10px 0 0;
}
.main-timeline .description{
    text-align: left;
    font-size: 1rem;
    letter-spacing: 1px;
    padding: 10px 20px 15px 50px;
    margin: 0;
}
.main-timeline .timeline-icon{
    background-color: #1b262c;
    font-size: 40px;
    text-align: center;
    line-height: 70px;
    height: 70px;
    width: 70px;
    border-radius: 100%;
    transform: translateY(-50%);
    position: absolute;
    left: -35px;
    top: 63%;
}
.company-icon {
  border-radius: 100%;
  height: 100%;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.main-timeline .timeline:nth-child(even){
    padding-left: 0;
    padding-right: 52px;
}
.main-timeline .timeline:nth-child(even):after{
    left: auto;
    right: 40.5%;
}
.main-timeline .timeline:nth-child(even) .timeline-content{ float: right; }
.main-timeline .timeline:nth-child(even) .timeline-icon{
    left: auto;
    right: -35px;
}
.main-timeline .timeline:nth-child(even) .description{ padding: 10px 50px 15px 20px; }

@media screen and (max-width:990px){
    .main-timeline .timeline:after{ left:40%; }
    .main-timeline .timeline:nth-child(even):after{ right:40%; }
    .main-timeline .timeline:nth-child(even) .timeline-content,
    .main-timeline .timeline-content{
        width: 39%;
    }
}
@media screen and (max-width:767px){
    .main-timeline:before{
        transform: translateX(0);
        left: 0;
    }
    .main-timeline .timeline:nth-child(even),
    .main-timeline .timeline{
        padding: 40px 30px 0;
        margin-bottom: 30px;
    }
    .main-timeline .timeline:before{
        transform: translateX(0) translateY(-50%);
        left: -11px;
    }
    .main-timeline .timeline:nth-child(even):after,
    .main-timeline .timeline:after{
        left: 28px;
    }
    .main-timeline .timeline:nth-child(even) .timeline-content,
    .main-timeline .timeline-content{
        width: 100%;
    }
    .main-timeline .timeline:nth-child(even) .timeline-icon,
    .main-timeline .timeline-icon{
        font-size: 30px;
        line-height: 50px;
        height: 50px;
        width: 50px;
        transform: translateY(0);
        left: auto;
        right: -20px;
        top: -35px;
    }
    .main-timeline .title{ font-size: 20px; }
    .main-timeline .timeline:nth-child(even) .description,
    .main-timeline .description{
        padding: 15px 15px 15px 15px;
    }
}
