.blinking {
    animation: opacity 2s infinite;
    opacity: 1;
}
@keyframes opacity {
  0% {
    opacity: 1;
  }
  12.5% {
    opacity: 0.875;
  }
  25% {
    opacity: 0.75;
  }
  37.5% {
    opacity: 0.625;
  }
  50% {
    opacity: 0.5;
  }
  62.5% {
  opacity: 0.625;
  }
  75% {
  opacity: 0.75;
  }
  87.5% {
    opacity: 0.875;
  }
     100% {
    opacity: 1;
  }
}
